* {
    padding: 0;
    margin: 0;
}

html {
    background-color: white;
    height: 100%;
}

body {
    position: relative;
    height: 100%;
    font-family: 'Roboto Mono', monospace;
    font-size: 14px;
}
.app {
    width: 100%;
    max-width: 768px;
    margin: 0px auto;
    color: #333;
    background-color: #f0f0f0;
}

@media only screen and (max-width: 768px) {
    .app {
        margin: 0px auto;
    }
}

header {
    background-color: #333;
    color: white;
}

header nav {
    display: flex;
}

header a {
    position: relative;
    width: 100%;
    color: inherit;
    padding: 0.5em 0;
    text-align: center;
    text-decoration: none;
}

.stories li {
    list-style-type: none;
}

.stories .storyLink {
    position: relative;
    padding: 0 0 1em 3em;
    text-decoration: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    display: inline-block;
    width: 100%;
    /* padding: 2.5em 0.5em; */
    box-sizing: border-box;
    font-weight: 400;
    border-bottom: 1px dashed rgba(0, 0, 0, 0.1);
}

.stories .storyLink:link {
    color: #333;
}

.stories .storyLink:visited {
    color: #99b;
}

.stories .storyLink:hover,
.stories .storyLink:active {
    background-color: #ffcdb3;
}

.stories .storyIcon {
    position: absolute;
    left: 0.75em;
    top: 0.25em;
}

.stories .storyDetails {
    margin-top: 0.3em;
    font-size: 0.7em;
    font-weight: 300;
    color: #333;
}

.stories .storyDetails span {
    white-space: nowrap;
}

.stories .storyIndex {
    display: inline-block;
    width: 2em;
}

.stories .nextPage {
    font-size: 1.1em;
    font-weight: 500;
    padding: 1em 0;
    cursor: pointer;
    text-align: center;
    outline: none;
    text-decoration: underline;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.stub {
    font-weight: 400;
    padding: 10em;
    text-align: center;
}
